
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


import { mapState, mapGetters } from "vuex";
export default {
directives: {
TransferDom
},
components: {
Group,
Cell,
Popup,
Checker,
CheckerItem,
Datetime,
Selector,

XButton
},
computed: {
...mapGetters([
"developPopup",
"applyList",
"branchList",
"developSelect",
"applyNum"
])
},
created() {
this.$store.dispatch("getBranchList");
},
activated() {
this.userid = window.localStorage.getItem("user");
if (this.$route.query.status == "apply") {
this.$store.dispatch("getApplyList", { cate: 1, user_id: this.userid });
} else if (this.$route.query.status == "positive") {
this.$store.dispatch("getApplyList", { cate: 2, user_id: this.userid });
} else if (this.$route.query.status == "prep") {
this.$store.dispatch("getApplyList", { cate: 3, user_id: this.userid });
}
this.ajaxBranch = this.branchList;
},
deactivated() {
this.currentPerson = [];
this.ajaxBranch = [];
},
data() {
return {
userid: "",
currentPerson: [],
isShow: false,
selectedDate: { key: 1, value: "本月" },
dateClass: [
{
key: 1,
value: "本月"
},
{
key: 2,
value: "本季度"
},
{
key: 3,
value: "本年"
}
],
startDate: "",
endDate: "",
ajaxBranch: [],
selectedBranch: "",
searchText: "",
ajaxPeople: [],
selectedPeople: "",
searchText2: ""
};
},
watch: {
developPopup: function(val) {
this.isShow = val;
},
applyList: function(val) {
this.currentPerson = val;
this.ajaxPeople = val;
},
branchList: function(val) {
this.ajaxBranch = val;
},
developSelect: function(val) {
this.currentPerson = val.data;
if (this.$route.query.status == "apply") {
this.applyNum[0].num = val.count;
} else if (this.$route.query.status == "positive") {
this.applyNum[1].num = val.count;
} else if (this.$route.query.status == "prep") {
this.applyNum[2].num = val.count;
}
},
startDate: function(val) {
if (val) {
this.selectedDate = {};
}
},
endDate: function(val) {
if (val) {
this.selectedDate = {};
}
},
selectedDate: function(val) {
if (Object.keys(val).length) {
this.startDate = "";
this.endDate = "";
}
},
$route(to, from) {
if (to.query.status == "apply") {
this.$store.dispatch("getAppLyNum", { user_id: this.userid });
this.$store.dispatch("getApplyList", { cate: 1, user_id: this.userid });
} else if (to.query.status == "positive") {
this.$store.dispatch("getAppLyNum", { user_id: this.userid });
this.$store.dispatch("getApplyList", { cate: 2, user_id: this.userid });
} else if (to.query.status == "prep") {
this.$store.dispatch("getAppLyNum", { user_id: this.userid });
this.$store.dispatch("getApplyList", { cate: 3, user_id: this.userid });
}
}
},
methods: {
hidePopup() {
this.$store.dispatch("updateDevelopPopup", {
developPopup: false
});
},
setToday(value) {
let now = new Date();
let cmonth = now.getMonth() + 1;
let day = now.getDate();
if (cmonth < 10) cmonth = "0" + cmonth;
if (day < 10) day = "0" + day;
this.startDate = now.getFullYear() + "-" + cmonth + "-" + day;
},
searchBranch(searchText) {
this.searchText = searchText;
},
searchPeople(searchText) {
this.searchText2 = searchText;
},
choosedData() {
if (!this.selectedDate && !this.startDate && !this.endDate) {
this.$vux.toast.text("请选择时间", "middle");
} else if (this.startDate && !this.endDate) {
this.$vux.toast.text("请选择结束时间", "middle");
} else if (!this.startDate && this.endDate) {
this.$vux.toast.text("请选择开始时间", "middle");
} else {
this.$store.dispatch("updateDevelopPopup", {
developPopup: false
});
if (this.$route.query.status == "apply") {
this.$store.dispatch("getDevelopSelect", {
cate: 1,
time: this.selectedDate.key,
start: this.startDate,
end: this.endDate,
department_id: this.selectedBranch,
applicant: this.selectedPeople
});
} else if (this.$route.query.status == "positive") {
this.$store.dispatch("getDevelopSelect", {
cate: 2,
time: this.selectedDate.key,
start: this.startDate,
end: this.endDate,
department_id: this.selectedBranch,
applicant: this.selectedPeople
});
} else if (this.$route.query.status == "prep") {
this.$store.dispatch("getDevelopSelect", {
cate: 3,
time: this.selectedDate.key,
start: this.startDate,
end: this.endDate,
department_id: this.selectedBranch,
applicant: this.selectedPeople
});
}
this.selectedDate = { key: 1, value: "本月" };
this.startDate = "";
this.endDate = "";
this.selectedBranch = "";
this.selectedPeople = "";
}
}
}
};



